export function createRunSummary(dryRun) {
    const actions = [];
    const consideredTasks = [];
    const candidates = [];
    return {
        dryRun,
        actions,
        consideredTasks,
        candidates,
        addAction(action) {
            actions.push(action);
        },
        addCandidateAssignPlan(login, message) {
            const normalized = login.trim();
            if (!normalized) {
                return;
            }
            let candidate = candidates.find((entry) => entry.login === normalized);
            if (!candidate) {
                candidate = { login: normalized, isAvailable: false, assignedIssueUrls: [], assignPlans: [] };
                candidates.push(candidate);
            }
            if (!candidate.assignPlans) {
                candidate.assignPlans = [];
            }
            candidate.assignPlans.push(message);
        },
        setConsideredTasks(tasks) {
            consideredTasks.length = 0;
            consideredTasks.push(...tasks);
        },
        setCandidates(next) {
            candidates.length = 0;
            candidates.push(...next);
        },
    };
}
