import { getStartStatus } from "../start-stop/get-start-status";
export class CandidatePool {
    _context;
    _orgCache = new Map();
    _orgMembers = null;
    _availability = new Map();
    _startStatus = new Map();
    constructor(context) {
        this._context = context;
    }
    getOrganizationCollaborators(org) {
        const trimmed = org.trim();
        if (!trimmed) {
            return Promise.resolve([]);
        }
        const cached = this._orgCache.get(trimmed);
        if (cached) {
            return cached;
        }
        const pending = this._context.adapters.getOrganizationCollaborators(trimmed);
        this._orgCache.set(trimmed, pending);
        return pending;
    }
    async getUsersByOrganization() {
        if (this._orgMembers) {
            return this._orgMembers;
        }
        this._orgMembers = (async () => {
            const map = new Map();
            for (const org of this._context.config.organizations) {
                const key = org.trim();
                if (!key) {
                    continue;
                }
                const members = await this.getOrganizationCollaborators(key);
                map.set(key, members);
            }
            return map;
        })();
        return this._orgMembers;
    }
    _getStartStatus(login, issueUrl) {
        const normalized = login.trim();
        if (!normalized) {
            return Promise.resolve(null);
        }
        const cached = this._startStatus.get(normalized);
        if (cached) {
            return cached;
        }
        const pending = getStartStatus(this._context, normalized, issueUrl);
        this._startStatus.set(normalized, pending);
        return pending;
    }
    _isGloballyAvailable(user, issueUrl) {
        const normalized = user.login.trim();
        if (!normalized) {
            return Promise.resolve(false);
        }
        const cached = this._availability.get(normalized);
        if (cached) {
            return cached;
        }
        const limit = this._context.config.assignedTaskLimit;
        const pending = this._getStartStatus(normalized, issueUrl).then((payload) => (payload?.computed.assignedIssues.length ?? 0) < limit);
        this._availability.set(normalized, pending);
        return pending;
    }
    async getAvailableCandidates(org, issueUrl) {
        const byOrg = await this.getUsersByOrganization();
        const members = byOrg.get(org.trim()) ?? [];
        const allowed = await Promise.all(members.map(async (member) => ((await this._isGloballyAvailable(member, issueUrl)) ? member : null)));
        return allowed.filter((login) => Boolean(login));
    }
    async getAllCandidateStatuses(issueUrl) {
        const byOrg = await this.getUsersByOrganization();
        const users = new Set();
        for (const list of byOrg.values()) {
            for (const user of list) {
                const normalized = user.login.trim();
                if (normalized) {
                    users.add(normalized);
                }
            }
        }
        const statuses = await Promise.all([...users].map(async (login) => {
            const payload = await this._getStartStatus(login, issueUrl);
            const assignedIssueUrls = (payload?.computed.assignedIssues ?? []).map((ref) => ref.html_url).filter((url) => Boolean(url));
            const isAvailable = assignedIssueUrls.length < this._context.config.assignedTaskLimit;
            return {
                login,
                isAvailable,
                assignedIssueUrls,
            };
        }));
        return statuses.sort((a, b) => a.login.localeCompare(b.login));
    }
    async getAllAvailableLogins(issueUrl) {
        const statuses = await this.getAllCandidateStatuses(issueUrl);
        return statuses.filter((entry) => entry.isAvailable).map((entry) => entry.login);
    }
}
